<template>
  <div class="col-lg-3 col-md-6 col-sm-6">
    <div id="wrap" class="w-block  w-block-inverse w-block-graph" :class="{'w-block-fixed': !isDashboard}">
      <div
        id="carousel-example-generic"
        class="carousel slide"
        data-ride="carousel"
      >
        <!-- Wrapper for slides -->
        <div class="carousel-inner" role="listbox">
          <div
            class="item active k-chart"
            id="graph_b14b653e0cb8470681cd746e688a4177"
            data-role="chart"
            style="position: relative; text-align: center"
          >
            <div style="font: 14px fira_sansregular; display: block; padding-top: 15px">Аналитика</div>
            <div style="display: block; height: 500px">
              <Pie
                      :chart-options="chartOptions"
                      :chart-data="chartData"
                      :chart-id="chartId"
                      :dataset-id-key="datasetIdKey"
                      :plugins="plugins"
                      :css-classes="cssClasses"
                      :styles="styles"
                      :width="width"
                      :height="height"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Pie } from "vue-chartjs/legacy";

  import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    ArcElement,
    CategoryScale,
  } from "chart.js";

  ChartJS.register(Title, Tooltip, Legend, ArcElement, CategoryScale);

export default {
  name: 'ActiveProgetsWidget',
  components: {
    Pie,
  },
  props: {
    chartId: {
      type: String,
      default: "pie-chart",
    },
    datasetIdKey: {
      type: String,
      default: "label",
    },
    width: {
      type: Number,
      default: 400,
    },
    height: {
      type: Number,
      default: 200,
    },
    cssClasses: {
      default: "",
      type: String,
    },
    styles: {
      type: Object,
      default: () => {},
    },
    plugins: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      chartData: {
        datasets: [
          {
            backgroundColor: ["#84FFFF", "#4141FF", "#9141E7"],
            data: [22, 35, 43],
          },
        ],
      },
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },
  computed: {
    isDashboard() {
      return this.height === 500;
    }
  }
};
</script>

