<script>
export default {
  name: 'DiscussionForm',
  data() {
    return {
      image: null,
      nameImage: '',
      sizeImage: '',
      content: '',
    };
  },
  methods: {
    changeFile(event) {
      this.image = event.target.files[0];
      const { name, size } = this.image;
      this.nameImage = name;
      this.sizeImage =
        size > 1024 ? `${Math.round(size / 1024)} кБайт` : `${size} байт`;
      let reader = new FileReader();
      reader.onload = this.onImageLoad;
      reader.readAsDataURL(this.image);
    },
    deleteFile() {
      const fileInput = this.$refs.fileInput;
      fileInput.type = 'text';
      fileInput.type = 'file';
      this.nameImage = '';
      this.sizeImage = '';
      this.image = null;
      this.content = null;
    },
    onImageLoad(e) {
      this.content = e.target.result;
    },
  },
};
</script>

