<script>
import SideBarApp from '@/components/shared/SideBarApp.vue';
import 'tabulator-tables/dist/css/tabulator_bootstrap5.min.css';
import JsonData from '../assets/data/subscriptions.json';

export default {
  name: 'Dashboard',
  data() {
    return {
      isToggled: false,
      count: {
        notification: 0,
        text: 0,
        valuation: 0,
        ending: 0,
        accepting: 0,
      },
      dashboardData: null,
    };
  },
  mounted() {
    JsonData.forEach((i) => {
      switch (i.stage) {
        case 'notification':
          this.count.notification += 1;
          break;
        case 'text':
          this.count.text += 1;
          break;
        case 'valuation':
          this.count.valuation += 1;
          break;
        case 'ending':
          this.count.ending += 1;
          break;
        case 'accepting':
          this.count.accepting += 1;
          break;
      }
    });
    this.changeData('notification');
  },
  methods: {
    selectTab(event, type) {
      let tabs = this.$el.querySelectorAll('.tab');
      tabs.forEach((el) => {
        el.classList.remove('tab-current');
      });

      if (!event.target.parentElement.classList.contains('tab')) {
        event.target.parentElement.parentElement.classList.add('tab-current');
      } else {
        event.target.parentElement.classList.add('tab-current');
      }

      this.changeData(type);
    },
    changeData(type) {
      let data = [];
      JsonData.forEach((i) => {
        if (i.stage == type) {
          data.push(i);
        }
      });
      if (data.length !== 0) {
        this.dashboardData = data;
      } else {
        this.dashboardData = null;
      }
    },
  },
  components: {
    SideBarApp,
  },
};
</script>

