<template>
  <div class="col-lg-3 col-md-6 col-sm-6">
    <div id="wrap" class="w-block  w-block-inverse w-block-graph" :class="{'w-block-fixed': !isDashboard}">
      <div id="carousel-orv" class="carousel slide" data-ride="carousel">
        <!-- Wrapper for slides -->
        <div class="carousel-inner" role="listbox">
          <div
                  class="item active k-chart"
                  id="graph_b14b653e0cb8470681cd746e688a4177"
                  data-role="chart"
                  style="position: relative; text-align: center"
          >
            <div style="font: 14px fira_sansregular; display: block; padding-top: 15px">Аналитика</div>
            <div :style="{display: 'block', height: '500'}">
              <Bar
                      :chart-options="chartOptions"
                      :chart-data="chartData"
                      :chart-id="chartId"
                      :dataset-id-key="datasetIdKey"
                      :plugins="plugins"
                      :css-classes="cssClasses"
                      :styles="styles"
                      :width="width"
                      :height="height"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Bar } from 'vue-chartjs/legacy'

import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale
} from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)

export default {
  name: 'OpbWidget',
  components: {
    Bar
  },
  props: {
    chartId: {
      type: String,
      default: 'bar-chart'
    },
    datasetIdKey: {
      type: String,
      default: 'label'
    },
    width: {
      type: Number,
      default: 400
    },
    height: {
      type: Number,
      default: 200
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object,
      default: () => {}
    },
    plugins: {
      type: Array,
      default: () => []
    },
    dashboard: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      chartData: {
        labels: [
          'Январь',
          'Февраль',
          'Март',
          'Апрель',
          'Май',
          'Июнь',
          'Июль',
          'Август',
          'Сентябрь',
          'Октябрь',
          'Ноябрь',
          'Декабрь'
        ],
        datasets: [
          {
            label: 'Проекты',
            backgroundColor: '#00FF00',
            data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11]
          }
        ]
      },
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false
      },
    }
  },
  computed: {
    isDashboard() {
      return this.height === 500;
    }
  }
};
</script>

<style scoped>
#wrap g > g > g > g > path {
  cursor: pointer;
}
</style>
