import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import Multiselect from 'vue-multiselect';
import VCalendar from 'v-calendar';
import './assets/styles/styles.scss';
import { vsCollapse, vsTooltip } from 'vuesax';
import 'vuesax/dist/vuesax.css';
import './assets/styles/aside.css';
import './assets/styles/main_css.css';

Vue.use(VCalendar, {
  componentPrefix: 'vc',
});
Vue.use(vsCollapse);
Vue.use(vsTooltip);

import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import {
  faPlay,
  faBook,
  faSquareCheck,
  faCircleInfo,
} from '@fortawesome/free-solid-svg-icons';
import { faHandPointDown } from '@fortawesome/free-regular-svg-icons';

library.add(faBook, faPlay, faHandPointDown, faSquareCheck, faCircleInfo);

Vue.component('font-awesome-icon', FontAwesomeIcon);
Vue.component('vc-multiselect', Multiselect);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
