import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '../views/HomeView.vue';
import ProjectsView from '../views/ProjectsView.vue';
import MaterialsView from '../views/MaterialsView.vue';
import MaterialItemView from '../views/MaterialItemView.vue';
import SupportView from '../views/SupportView.vue';
import ProjectsDashboard from '../views/ProjectsDashboard.vue';
import Auth from '../views/Auth';
import Registration from '../views/Registration';
import Dashboard from '@/views/Dashboard';
import Analytics from "@/views/Analytics";
import Publications from "@/views/Publications";

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/projects',
    name: 'projects',
    component: ProjectsView,
  },
  {
    path: '/regulation/materials',
    name: 'materialsList',
    component: MaterialsView,
  },
  {
    path: '/regulation/materials/:id',
    name: 'materialItem',
    component: MaterialItemView,
  },
  {
    path: '/regulation/UserSupport/',
    name: 'support',
    component: SupportView,
  },
  {
    path: '/dashboard/projects',
    name: 'projectsDashboard',
    component: ProjectsDashboard,
  },
  {
    path: '/dashboard/',
    name: 'dashboard',
    component: Dashboard,
  },
  {
    path: '/auth/',
    name: 'auth',
    component: Auth,
  },
  {
    path: '/registration/',
    name: 'registration',
    component: Registration,
  },
  {
    path: '/dashboard/analytics',
    name: 'analytics',
    component: Analytics,
  },
  {
    path: '/dashboard/publications',
    name: 'publications',
    component: Publications,
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
