<template>
  <div class="dashboard-widget">
    <div class="row widget-content-wrap k-widget k-listview">
      <card-project
        v-for="projectData in viewProjects"
        :key="projectData.id"
        :projectData="projectData"
        :openProject="openProject"
      />
    </div>
    <paging
      :selectedPage="selectedPage"
      :maxOnPage="projectInPage"
      :total="projectsData.length"
    />
    <modal-layout
      v-show="isShowProject"
      title="Проект"
      layout="low_modal"
      :closeModal="closeProject"
    >
      <stages-project
        :openDiscussion="openDiscussion"
        :openPassport="openPassport"
        :openStageProjectInfo="openStageProjectInfo"
        :openProjectText="openProjectText"
      />
    </modal-layout>
    <modal-layout
      v-show="isShowDiscussion"
      title="Обсуждение"
      layout="middle_modal"
      :closeModal="closeDiscussion"
    >
      <discussion-project :openAnswer="openAnswer" />
    </modal-layout>
    <modal-layout
      v-show="isShowAnswer"
      title="Ответ"
      layout="high_modal"
      :closeModal="closeAnswer"
    >
      <discussion-form />
    </modal-layout>
    <modal-layout
      v-show="isShowPassport"
      title="Паспорт проекта"
      layout="high_modal"
      :closeModal="closePassport"
    >
      <passport-project />
    </modal-layout>
    <modal-layout
      v-show="isShowStageProjectInfo"
      title="Информация по этапу"
      layout="high_modal"
      :closeModal="closeStageProjectInfo"
    >
      <stage-project-info />
    </modal-layout>
    <modal-layout
      v-show="isShowProjectText"
      title=""
      layout="high_modal"
      :closeModal="closeProjectText"
    >
      <web-viewer
        url="https://pdftron.s3.amazonaws.com/downloads/pl/demo-annotated.pdf"
      />
    </modal-layout>
  </div>
</template>

<script>
import ModalLayout from '@/layouts/ModalLayout.vue';
import CardProject from './CardProject.vue';
import { PROJECTS_DATA } from '../../const';
import Paging from './Paging.vue';
import DiscussionProject from '@/components/projects/DiscussionProject.vue';
import StagesProject from '@/components/projects/StagesProject.vue';
import DiscussionForm from './DiscussionForm.vue';
import PassportProject from './PassportProject.vue';
import StageProjectInfo from './StageProjectInfo.vue';
import WebViewer from '../WebViewer.vue';

const PROJECTS_IN_PAGE = 9;

export default {
  name: 'MainProjects',
  components: {
    CardProject,
    ModalLayout,
    Paging,
    DiscussionProject,
    StagesProject,
    DiscussionForm,
    PassportProject,
    StageProjectInfo,
    WebViewer,
  },
  data() {
    return {
      projectsData: PROJECTS_DATA,
      isShowDiscussion: false,
      isShowProject: false,
      isShowAnswer: false,
      isShowPassport: false,
      isShowStageProjectInfo: false,
      isShowProjectText: false,
      currentProject: null,
      page: 1,
      projectInPage: PROJECTS_IN_PAGE,
    };
  },
  methods: {
    openProject(projectData) {
      this.isShowProject = true;
      this.currentProject = projectData;
    },
    closeProject() {
      this.isShowProject = false;
      this.currentProject = null;
    },
    openDiscussion() {
      this.isShowDiscussion = true;
    },
    closeDiscussion() {
      this.isShowDiscussion = false;
    },
    openAnswer() {
      this.isShowAnswer = true;
    },
    closeAnswer() {
      this.isShowAnswer = false;
    },
    openPassport() {
      this.isShowPassport = true;
    },
    closePassport() {
      this.isShowPassport = false;
    },
    openStageProjectInfo() {
      this.isShowStageProjectInfo = true;
    },
    closeStageProjectInfo() {
      this.isShowStageProjectInfo = false;
    },
    openProjectText() {
      this.isShowProjectText = true;
    },
    closeProjectText() {
      this.isShowProjectText = false;
    },
    selectedPage(page) {
      this.page = page;
    },
  },
  computed: {
    viewProjects() {
      const startView = (this.page - 1) * this.projectInPage;
      const endView = startView + PROJECTS_IN_PAGE;
      return PROJECTS_DATA.slice(startView, endView);
    },
  },
};
</script>
