<template>
  <div id="wrapper" :class="{ toggled: isToggled }">
    <side-bar-app @toggle="isToggled = !isToggled" @filter="filterData" />
    <div class="container bs-docs-container width-100" style="height: 619px">
      <div class="container-fluid">
        <div class="row" style="height: 100%">
          <div
            class="col-md-12"
            role="main"
            style="height: 100%"
            id="table"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SideBarApp from '@/components/shared/SideBarApp.vue';
import { TabulatorFull as Tabulator } from 'tabulator-tables';
import 'tabulator-tables/dist/css/tabulator_bootstrap5.min.css';
import JsonData from '../assets/data/projects.json';

export default {
  name: 'ProjectsDashboard',
  data() {
    return {
      isToggled: false,
      table: null,
    };
  },
  mounted() {
    let data = JsonData;
    let filteredData = [];
    let stage = this.$route.query.stage;
    data.forEach((i) => {
      if (i.stage == stage) {
        filteredData.push(i);
      } else if (stage == 'Все') {
        filteredData.push(i);
      }
    });
    this.table = new Tabulator('#table', {
      locale: true,
      langs: {
        'ru-ru': {
          data: {
            loading: 'Загрузка',
            error: 'Ошибка',
          },
          pagination: {
            page_size: 'Размер страницы',
            page_title: 'Показать страницу',
            first: 'Первая',
            first_title: 'Первая страница',
            last: 'Последняя',
            last_title: 'Последняя страница',
            prev: 'Пред.',
            prev_title: 'Пред. Страница',
            next: 'След.',
            next_title: 'След. Страница',
            all: 'Все',
            counter: {
              showing: 'Отображено',
              of: 'из',
              rows: 'записей',
              pages: 'страниц',
            },
          },
          headerFilters: {
            default: 'Отфильтровать колонку...',
            columns: {
              name: 'Фильтрация по названию...',
            },
          },
        },
      },
      data: filteredData,
      layout: 'fitColumns',
      responsiveLayout: 'hide',
      tooltips: true,
      addRowPos: 'top',
      history: true,
      pagination: 'local',
      paginationSize: 7,
      paginationCounter: 'rows',
      movableColumns: true,
      initialSort: [{ column: 'name', dir: 'asc' }],
      columns: [
        { title: 'Разработчик', field: 'developer', hozAlign: 'left' },
        { title: 'Наименование', field: 'name', formatter: 'textarea' },
        { title: 'Этап', field: 'stage', hozAlign: 'center' },
        { title: 'Статус', field: 'status' },
        {
          title: 'Регионально значимый',
          field: 'regionalImportance',
          width: 90,
          sorter: 'date',
          hozAlign: 'center',
          formatter: 'tickCross',
        },
        {
          title: 'Контрольно-надзорная деятельность',
          field: 'CSA',
          width: 90,
          hozAlign: 'center',
          formatter: 'tickCross',
          sorter: 'boolean',
        },
        {
          title: 'Регуляторная гильотина',
          field: 'RegulatoryGuillotine',
          width: 90,
          formatter: 'tickCross',
        },
        { title: 'Вид', field: 'from', width: 130, formatter: 'textarea' },
        { title: 'ОКВЭД', field: 'OKVED', width: 130, formatter: 'textarea' },
      ],
    });

    this.table.on('rowClick', function (e, row) {
      let rowData = row.getData();
    });
  },
  methods: {
    filterData(stage) {},
  },
  components: {
    SideBarApp,
  },
};
</script>

