<template>
  <div>
    <header-app :mainPage="false" />
    <div class="root-wrapper">
      <div class="wrapper wrapper--expanded">
        <div class="container" v-html="content"></div>
      </div>
      <footer-app />
    </div>
  </div>
</template>
<script>
import HeaderApp from '@/components/shared/HeaderApp.vue';
import FooterApp from '@/components/shared/FooterApp.vue';
import JsonData from '@/assets/data/materials.json';

export default {
  name: 'MaterialItemView',
  data() {
    return {
      jsonData: JsonData,
      id: this.$route.params.id,
      content: '',
    };
  },
  mounted() {
    this.content = this.jsonData.materials[this.id].content;
  },
  components: {
    HeaderApp,
    FooterApp,
  },
};
</script>

