<template>
  <div class="k-pager-wrap k-widget">
    <span
      title="Первая страница"
      class="k-link k-pager-nav k-pager-first"
      :class="{ 'k-state-disabled': isFirstPage }"
      @click="intoFirstPage()"
    >
      <span class="k-icon k-i-seek-w">Первая страница</span>
    </span>
    <span
      title="Предыдущая"
      class="k-link k-pager-nav"
      :class="{ 'k-state-disabled': isFirstPage }"
      @click="prevPage()"
    >
      <span class="k-icon k-i-arrow-w">Предыдущая</span>
    </span>
    <ul class="k-pager-numbers k-reset">
      <li v-for="pageNumber in numberPages" :key="new Date() + pageNumber">
        <span v-if="pageNumber === currentPage" class="k-state-selected">
          {{ pageNumber }}
        </span>
        <a
          href=""
          v-if="pageNumber !== currentPage"
          class="k-link"
          @click.prevent="changePage(pageNumber)"
        >
          {{ pageNumber }}
        </a>
      </li>
      <!-- <li>
        <a
          tabindex="-1"
          href="#"
          class="k-link"
          data-page="11"
          title="More pages"
          >...</a
        >
      </li> -->
    </ul>
    <span
      title="Следующая"
      class="k-link k-pager-nav"
      :class="{ 'k-state-disabled': isLastPage }"
      @click="nextPage()"
    >
      <span class="k-icon k-i-arrow-e">Следующая</span>
    </span>
    <span
      title="Последняя страница"
      class="k-link k-pager-nav k-pager-last"
      :class="{ 'k-state-disabled': isLastPage }"
      @click="intoLastPage()"
    >
      <span class="k-icon k-i-seek-e">Последняя страница</span>
    </span>
    <span class="k-pager-info k-label">{{ pagingText }}</span>
  </div>
</template>

<script>
export default {
  name: 'Paging-control',
  props: {
    selectedPage: {
      type: Function,
      required: true,
    },
    total: {
      type: Number,
      required: true,
    },
    maxOnPage: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      currentPage: 1,
    };
  },
  computed: {
    numberPages() {
      return Math.ceil(this.total / this.maxOnPage);
    },
    isFirstPage() {
      return this.currentPage === 1;
    },
    isLastPage() {
      return this.currentPage === this.numberPages;
    },
    pagingText() {
      const firstItem = (this.currentPage - 1) * this.maxOnPage + 1;
      let lastItem = firstItem + this.maxOnPage - 1;
      if (lastItem > this.total) {
        lastItem = this.total;
      }
      const isRange = firstItem !== lastItem;
      return isRange
        ? `${firstItem} - ${lastItem} из ${this.total}`
        : `${firstItem} из ${this.total}`;
    },
  },
  methods: {
    changePage(pageNumber) {
      this.currentPage = pageNumber;
      this.selectedPage(pageNumber);
    },
    nextPage() {
      if (!this.isLastPage) {
        this.currentPage += 1;
        this.selectedPage(this.currentPage);
      }
    },
    prevPage() {
      if (!this.isFirstPage) {
        this.currentPage -= 1;
        this.selectedPage(this.currentPage);
      }
    },
    intoFirstPage() {
      this.currentPage = 1;
      this.selectedPage(this.currentPage);
    },
    intoLastPage() {
      this.currentPage = this.numberPages;
      this.selectedPage(this.currentPage);
    },
  },
};
</script>
