<template>
  <div id="webviewer" ref="viewer"></div>
</template>

<script>
import WebViewer from '@pdftron/webviewer';
export default {
  name: 'WebViewer',
  props: {
    url: String,
  },
  mounted() {
    WebViewer(
      {
        path: `${process.env.BASE_URL}webviewer`,
        initialDoc: this.url,
        disabledElements: ['viewControlsButton', 'viewControlsOverlay'],
      },
      this.$refs.viewer
    ).then((instance) => {
      const { Core, UI } = instance;
      UI.disableElements([
        'leftPanel',
        'leftPanelButton',
        'selectToolButton',
        'panToolButton',
        'ribbons',
        'searchButton',
        'toggleNotesButton',
        'languageButton',
        'themeChangeButton',
        'contextMenuPopup',
        'annotationPopup',
        'textPopup',
        'searchPanel',
        'toolsHeader',
      ]);
      UI.setLanguage('ru');
    });
  },
};
</script>

<style>
#webviewer {
  height: 80%;
}
</style>
